.main{
    background-color: var(--background);
}
.mydark
{
    margin-top: 20px;
    margin-right: 30px;

    
}
.about-me
{
    width: 100%;
    height: 500px;
    
}
.intropara
{
    color: var(--text)
}
.profile-image
{
    padding: 10px;
}
.avatar
{
    border: 6px solid #1b1a1a;
  border: 6px solid #8f19cf;
  vertical-align: middle;
  width: 270px;
  height: 270px;
  border-radius: 50%;
  box-shadow: 0 6px 10px 10px rgba(0, 0, 0, 0.2), 0 8px 20px 10px rgba(0, 0, 0, 0.19);
  padding: 5px;
}
h1{
    margin-top: 17px;
    font-size: 50px;
    font-family: 'Bai Jamjuree', sans-serif;
    font-weight: bold;
    color: #fff;
		background: linear-gradient(to right, #ff0000 10%,  #8f19cf 50%);
		background-size: auto auto;
		background-clip: border-box;
		background-size: 200% auto;
		color: #fff;
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		animation: textclip 1.5s linear infinite;
		display: inline-block;
    
}

@keyframes textclip {
	to {
		background-position: 200% center;
	}
}

p{
    font-family: 'Tinos', serif;
    margin-top: 17px;
    font-size: 19px;
}

.wave
{
    font-size: x-large;
    color: #8f19cf;
    font-weight: bold;
}

.projects
{
    width: 100%;
    margin-top: 40px;
    
}
.myprojects
{
    width: 100%;
}
.icon-down
{
    font-size: 35px;
    color: #8f19cf;
    display: inline-block;
    cursor: pointer;
  position: relative;
  -moz-animation: bounce 2s infinite linear;
  -o-animation: bounce 2s infinite linear;
  -webkit-animation: bounce 1s infinite linear;
  animation: bounce 2s infinite linear;
  color:000;
}
.display-pic
{
    width: 27%;
    height: 100px;
    margin: 15px auto;
}
.card-hi
{
    padding: 15px;
    margin-top: 25px;
    background-color: var(--cardbackground);
}

h4
{
    margin-top: 20px;
    font-family: 'Tinos', serif;
    font-weight: bold;
    color: #8f19cf;
}

.button {
    margin: 18px auto;
    width: 250px;
    cursor: pointer;
    font-size: 110%;
    background: #8f19cf;
    color: #fff;
    border: 1px solid #8f19cf;
    border-radius: 10px;
    height: 60px;
    font-family: 'Tinos', serif;
    }

    .button:hover {
    color: white;
    background: #ff0000;
    border: 1px solid #fff;
    }

    .main
    {
        display: flex;
        flex-direction: column;
        min-height: 100vh;

    }

    .my-content
    {
        flex: 1;
    }


@-webkit-keyframes bounce {
    0% { top: 0; }
    50% { top: -0.2em; }
    70% { top: -0.3em; }
    100% { top: 0; }
}
@-moz-keyframes bounce {
    0% { top: 0; }
    50% { top: -0.2em; }
    70% { top: -0.3em; }
    100% { top: 0; }
}
@-o-keyframes bounce {
    0% { top: 0; }
    50% { top: -0.2em; }
    70% { top: -0.3em; }
    100% { top: 0; }
}
@-ms-keyframes bounce {
    0% { top: 0; }
    50% { top: -0.2em; }
    70% { top: -0.3em; }
    100% { top: 0; }
}
@keyframes bounce {
    0% { top: 0; }
    50% { top: -0.2em; }
    70% { top: -0.3em; }
    100% { top: 0; }
}

@media only screen and (max-width: 900px) {
    p{
    font-size: 17px;
    }
    
    h1
    {
        font-size: 45px;
    }

    .projects
    {
        margin-top: 2px;
    }
    .avatar
    {
        width: 250px;
        height: 250px;
    }
  }

  @media only screen and (max-width: 1024px) {

    .button {
        margin: 18px auto;
        width: 200px;
        cursor: pointer;
        font-size: 95%;
        background: #8f19cf;
        color: #fff;
        border: 1px solid #8f19cf;
        border-radius: 10px;
        height: 50px;
        font-family: 'Tinos', serif;
        }
   
  }

  @media only screen and (max-width: 700px) {
    p{
    font-size: 16px;
    }
    
    h1
    {
        font-size: 45px;
    }

    .projects
    {
        margin-top: 18px;
    }
    .avatar
    {
        width: 200px;
        height: 200px;
    }

    .card-hi
    {
      
       margin-top: 25px;
    }
    
  }


  